@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input:focus {
  outline: none;
}
* {
  text-decoration: none;
}

.submitLanding-container {
  width: 100%;
  margin: auto;
}
.submitLanding-title {
  width: 100%;
  text-align: center;
  margin-bottom: 18px;
  margin-top: 10px;
  padding: 10px 0px;
  color: rgb(33, 37, 41);
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}
.submitLanding-title p {
  font-size: 38px;
  line-height: 42px;
  font-weight: 700;
  color: #4b2272;
}
.submitLanding-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.submitLanding-wrapper .info-wrapper,
.submitLanding-wrapper .form-wrapper {
  width: 47%;
  min-width: 400px;
  margin: 12px;
  color: #444444;
  background: rgb(252, 251, 255);
  padding: 30px;
}
@media screen and (max-width: 400px) {
  .submitLanding-wrapper .info-wrapper,
  .submitLanding-wrapper .form-wrapper {
    width: 47%;
    min-width: 300px;
    margin: 12px;
    color: #444444;
    background: rgb(252, 251, 255);
    padding: 30px;
  }
}
.submitLanding-wrapper .info-wrapper .title {
  font-size: 20px;
  color: #012970;
  font-weight: 700;
  margin: 20px 0 10px 0;
  line-height: 1.2;
}
.submitLanding-wrapper .info-wrapper p {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  line-height: 24px;
  font-size: 14px;
}
.submitLanding-wrapper .form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.submitLanding-wrapper .enter-userInfo {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.submitLanding-wrapper .enter-userInfo input:nth-child(1) {
  margin-right: 10px;
}
.submitLanding-wrapper .enter-userInfo input:nth-child(2) {
  margin-left: 10px;
}
.submitLanding-wrapper .form-wrapper .form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.submitLanding-wrapper .form-wrapper .form-control:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgb(13 110 253 / 25%);
}
.submitLanding-wrapper .enter-message {
  margin-top: 24px;
  width: 100%;
  min-height: calc(1.5em + 0.75rem + 2px);
}
.submitLanding-wrapper .get-permission {
  margin: 16px;
  margin-left: 0px;
  width: 100%;
  padding-left: 25px;
  position: relative;
}
.submitLanding-wrapper .get-permission input {
  position: absolute;
  margin: 2px;
  left: 1px;
  top: -1px;
  width: 14px;
  height: 14px;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.submitLanding-wrapper .get-permission label {
  display: inline-block;
  font-size: 16px;
  line-height: 20px;
}
.submitLanding-wrapper .submit-wrapper {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  width: 100%;
}
.submitLanding-wrapper .submit-wrapper button {
  background-color: #7c659e;
  border: 0;
  border-radius: 40px;
  width: 200px;
  height: 45px;
  text-align: center;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

@media screen and (max-width: 850px) {
  .submitLanding-title {
    padding: 0px 30px;
  }
  .submitLanding-title p {
    font-size: 30px;
  }
  .submitLanding-wrapper .info-wrapper,
  .submitLanding-wrapper .form-wrapper {
    width: calc(100% - 60px);
    margin: 0px 30px;
    margin-bottom: 20px;
    padding: 30px !important;
    min-width: 50px;
  }
}
@media screen and (max-width: 500px) {
  .submitLanding-wrapper .enter-userInfo {
    flex-direction: column;
  }
  .submitLanding-wrapper .enter-userInfo input:nth-child(1) {
    margin: 0px;
    margin-bottom: 10px;
  }
  .submitLanding-wrapper .enter-userInfo input:nth-child(2) {
    margin: 0px;
  }
}

