.permission-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.permission-wrapper {
  width: 30%;
  height: 30%;
  min-width: 360px;
  min-height: 240px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: wheat;
  border: none;
  border-radius: 7%;
}
.permission-wrapper input {
  border: none;
  border-bottom: 3px solid rgba(255, 255, 255, 0.8);
  font-size: 24px;
  color: #fff;
  font-weight: 600;
  background-color: wheat;
  text-align: center;
}
.permission-wrapper input:focus {
  outline: none;
  text-align: center;
}
.permission-wrapper input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.4;
}
.permission-wrapper input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.4;
}
.permission-wrapper input::placeholder {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.4;
}

